<!-- eslint-disable vue/multi-word-component-names -->
<template>
  <div>
    <!-- url정보는 $route에 담겨있음. .params는 여려개의 정보를 의미함. -->
    {{ $route.params.id }}
    <div>
      <h4>상세페이지</h4>
      <h5>{{ BlogW[$route.params.id].title }}</h5>
      <!-- 접속한 url에 따라 내용이 다르게 보여짐. -->
      <p>{{ BlogW[$route.params.id].content }}</p>
    </div>
    <!-- children:{}안에 있는 컴포넌트를 보여줄 위치 -->
    <router-view></router-view>
  </div>
</template>
<script>
export default {
  props: {
    BlogW: Array
  }
}
</script>
